/* number */

.number {
    background-color: #10c9c3;
}

.number__inner {
    padding: 50px 0;
}


.number__item {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 69px;
    &:first-child {
        margin-left: 0;
    }
}
.number__row {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
} 

.number__item-text {
    color: #ffffff;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    padding-left: 28px;
    line-height: 30px;
}
.number__item-text > span {
    font-size: 12px;
}

@media (max-width: 768px) {
    .number__wrapper {
        padding: 0;
    }
}
@media (max-width: 991px) {
    .number__wrapper{
            padding: 50px 50px;
        }
    .number{
        &__item {
            margin-left: 20px;
            &-text {
                line-height: 20px;
                padding-left: 14px;
            }
        }
        &__image {
            img {
                width: 100%;
            }
        }
    }
}



